import PeopleCard from "./PeopleCard";

import abelImg from '@assets/img/people/abel.png';
import juanImg from '@assets/img/people/juan.png';
import juanjoImg from '@assets/img/people/juanjo.jpg';
import julianImg from '@assets/img/people/julian.png';


const Leaders: React.FC = () => {
    return (
        <div className="container pt-8 flex gap-8 gap-x-16 flex-wrap justify-center">
            <div className="divider text-3xl w-full">Lderes</div>
            <PeopleCard name="Angel Abel Arismendy" title="Ingeniera de Sistemas" roles={["Lder",  "Monitor de Colivri", "Desarrollador Web", "Diseador grfico"]} image={abelImg} url="https://abel.arismendy.co"/>
            <PeopleCard name="Juan Esteban Rodriguez" title="Ingeniera de Sistemas" roles={["Lder", "Monitor de Vivero Virtual"]} image={juanImg}/>
            <PeopleCard name="Juan Jos Sierra" title="Ingeniera de Sistemas" roles={["Lder", "Monitor de Videojuegos"]} image={juanjoImg}/>
            <PeopleCard name="Julian Mora" title="Maestra en Ingeniera de Sistemas" roles={["Lder", "Asistente Graduado"]} image={julianImg}/>
        </div>
    )
}

export default Leaders;